@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(90deg, rgba(201,187,207,1) 22%, rgba(214,239,237,1) 76%);
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

img {
  min-width: 175px;
}

h1 {
  text-align: center;
  font-size: max(2.5vw, 2em, 2rem);
}

button {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  background-color: #fff;
  color: #506491;
  padding: 0.5vw 0.5vw;
  font-size: max(2vw, 1.5em, 1.5rem);
  border: 3px solid #506491;
  border-radius: 5px;
  transition: background-color 0.4s;
}

button:hover {
  background-color: #506491;
  color: #fff;
  cursor: pointer;
}

button:active {
  transform: scale(0.9);
}

.title {
  text-align: center;
  font-size: max(7vw, 4em, 4rem);
}

nav {
  background-color: #506491;
  color: #fff;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  padding: 0px 1rem;
  align-items: center;
}

nav ul {
  list-style: none;
  margin: 0;
  display: flex;
  gap: 1rem;
}

nav a {
  color: #fff;
  font-size: max(2vw, 30px);
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s;
}

nav a:hover {
  color: #A78DAD;
}